body{
	height: 100vh;
	overflow: hidden;
}
p{
	margin: 0;
	padding: 0;
}
.demologo{
	position: absolute;
	z-index: 999;
	width:10%;
	margin-left: 50px;
	margin-top: -10px;
}
.menu-bar-main{
	position: absolute;	
	display: -ms-flexbox;	
	display: flex;
	-ms-flex-pack: end;
	    justify-content: flex-end;
	width: 80%;
	margin-left: 20%;	
	text-align: center;	
	z-index: 99999;
	padding: 10px 30px;	
	-webkit-box-sizing: border-box;	
	        box-sizing: border-box;

}
.menu-bar-but{
	padding:3px 4px;
	margin: 0 2px;
}
.load-data-but{
	position: relative;
	text-align: center;	
	border: 1px solid transparent;
	font-size: 20px;
	text-decoration: underline;
}
.load-data-but:hover{
	cursor: pointer;
	border: 1px solid black;
	
}
.load-data-but:hover .load-data-tooltip{
	visibility: visible;
}
.tooltip{
	position: absolute;	
	visibility: hidden;
	left: -3px;

}
.ldm-main,
.fdm-main,
.cpm-main{
	width: 150px;
	max-height: 70vh;	
	padding:0 0 15px 0;
	position: absolute;
	background-color: #eee;
	right: 10px;	
	margin-top:60px;
	border: 3px solid #09f;
	z-index: 9999;
	overflow-y: scroll;
	overflow-x: hidden;
}
.ldm-header,
.fdm-header,
.cpm-header{
	padding: 5px;	
	border-bottom: 2px solid #09f;
	margin: 0;	
}
.ldm-close,
.fdm-close,
.cpm-close{
	float: right;	
	color:red;
	margin-right: 5px;	
	font-weight: bolder;
	font-size: 20px;
}
.ldm-close:hover,
.fdm-close:hover,
.cpm-close:hover{
	cursor: pointer;
}
.ldm-list-container{	
	background-color: 	white;
	height: 80%;	
	margin: 20px 10px;
	border: 3px solid #aaa;
	overflow: auto;
}
.state-list-display{
	background-color: 	white;
	height: 100px;	
	margin: 10px 10px 5px 10px;
	border: 3px solid #09f;
	overflow: auto;
}
.state-list-display-li:hover{
	background-color: #5bf;
	cursor: pointer;
}
.ldm-li{
	padding: 2px 5px;
	font-size: small;

}
.ldm-li-tableName{
	text-decoration: underline;
	display: block;
}
.ldm-li:hover{
	background-color: 	#5bf;
	cursor: pointer;
}
.include-exclude-but, 
.add-state-but{
	width: 85%;
	margin: auto;
	color: white;
	padding: 1px 6px;
	text-align: center;
	background-color: #09f;
	border: 2px solid #09f;
	border-radius: 10px;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	margin-bottom: 5px;
}

.include-exclude-but:hover,
.add-state-but:hover{
	border: 2px solid black;
	cursor: pointer;
}
.add-state-but:hover .state-select-container{
	display: block;
}
.add-state-but{
	position: relative;
}
.state-select-container{
	position: absolute;
	display: none;
	height: 200px;
	overflow: auto;
	margin: auto;
	color: white;
	padding: 1px 6px;
	text-align: center;
	background-color: #09f;
	border: 2px solid black;
	margin-bottom: 5px;
	left: 0;
}
.state-select-li{
	border-bottom: 1px solid black;
}
.state-select-li:hover{
	border-bottom: 1px solid white;
}
.fdm-input-container{
	font-size: 13px;
	width: 85%;
	margin: auto;
	text-align: center;
	border: 2px solid #09f;
	padding: 3px 0;
}
.fdm-input{
	width: 30px;
	margin-left: 3px;
}
.spacer{
	margin-top: 6px;
}
.set-filter-but{
	text-align: center;
	width: 85%;
	margin: auto;
	border: 2px solid #09f
}
.set-filter-but:hover{
	cursor: pointer;
	background-color: #09f;
	border: 2px solid black;
	color: white;
}
.flexbox-fix:nth-child(4){
	display: none !important;
}
.color-picker{
	width: 140px;
	margin: auto;
	text-align: center;
}
.color-range-main{
	top: 20px;
	position: relative;
	width: 37.5%;	
	height:18px;
	border:1px solid black;
	margin-right: 43.75%;
	border-radius: 10px;
	text-align: center;	
}
.color-range-inner{
	position: absolute;
	width: 80%;	
	background-color: white;
	height:100%;
	left:10%;
}
.data-min{	
	left:0px;
	border-top-left-radius: 10px;
	border-bottom-left-radius: 10px;
	border-right:1px solid black;
}
.data-max{	
	right:0px;
	border-top-right-radius: 10px;
	border-bottom-right-radius: 10px;
	border-left:1px solid black;
}
.data-max,
.data-min{
	position: absolute;
	width: 10%;	
	height: 100%;
	background-color: #ccc;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}
.data-display{
	position: absolute;
	width: 10%;	
	left:45%;
	top:19px;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	background-color: #ccc;
	border:1px solid black;
	border-top:none;
}
@media screen and (max-width: 1450px){
	.color-range-main{
		width: 500%;	
		margin-right: 25%;
	}

}
@media screen and (max-width: 1000px){
	.data-max,
	.data-min{
		width:15%;
	}
	.data-display{
		width: 15%;	
		left:40%;
	}
	.color-range-inner{
		width: 70%;	
		left:15%;
	}
}
@media screen and (max-width: 750px){
	.color-range-main{	
		margin-right: 15%;
	}
}
@media screen and (max-width: 650px){
	.color-range-main{	
		margin-right: 0%;
	}
	.menu-bar-main{
		width: 90%;	
		margin-left: 10%;
	}
}

.slider{
	position: absolute;
	top:-5px;
}
.slider-image{
	height:28px;
	width: 40px; 
}



